
@media screen {
  * {
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
  }
  
  img { 
    pointer-events: none; 
  }
  
  
  body {
    background-color:black;
  }
  
  @font-face {
    font-family: 'Montserrat';
    src: url(https://fonts.gstatic.com/s/montserrat/v25/JTUHjIg1_i6t8kCHKm4532VJOt5-QNFgpCtr6Hw9aXpsog.woff2) format('woff2');
  }
  
  @font-face {
    font-family: 'Roboto';
    src: url(https://fonts.gstatic.com/s/roboto/v30/KFOkCnqEu92Fr1MmgVxMIzIFKw.woff2) format('woff2');
  }
  
  .first {
    width: 654px;
    height: 98px;
    font-size: 20px;
    font-family: 'Montserrat';
    color: white;
    font-style: normal;
    margin-left: 20%;
  }
  
  .second {
    margin-left: 20%;
    width: 654px;
    height: 98px;
    font-size: 15px;
    font-family: 'Roboto';
    font-style: normal;
    color: rgba(255, 255, 255, 0.6);
    
  }
  
  .buttons {
    margin-left: 20%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  
  
  .tg {
    padding-left: 10%;
    
  }
  
  .copyright {
    font-family: 'Roboto';
    color: white;
    position: fixed; /* Фиксированное положение */
    left:43%;
    bottom: 0; /* Левый нижний угол */
  }
  
  .iphone {
    margin-top: 5%;
    margin-left: 10%;
  }
  
  .container {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  
  .i {
    width: 90%;
    height: 90%;
  }
  
  
}

